











































































































































































































@import '@design';

.block {
  border-bottom: 1px solid #fff;
  span {
    margin: 0 7px;
    div {
      display: inline;
    }
  }
  label {
    padding-right: 10px;
    font-weight: 600;
  }
}

.noloThreshold {
  display: inline-block;
  width: 5rem;
  text-align: right;
}

.warning {
  color: #c44d01;
  background-color: #fff;
}

.xlsxpreview {
  width: 90%;
  margin-top: 25px;
  table {
    max-height: 75vh;
    padding: 25px;
    overflow: auto;
    color: #626262;
    background-color: #fff;

    tbody {
      tr:nth-child(odd) {
        background-color: #d4e5f0;
      }

      tr[nolomaybe] {
        background-color: #f6dccb;
      }
    }
  }
  table[concise] tbody tr {
    td {
      display: none;
    }
    td:nth-child(5),
    td:nth-child(6),
    td:nth-child(7),
    td:nth-child(15),
    td:nth-child(16),
    td:nth-child(25) {
      display: table-cell;
    }
    td:nth-child(25) {
      text-align: right;
    }
  }
  p {
    line-height: 1.5rem;
  }
}
